    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default {  
		data() {
			return {
				memberInfo:[],
				opwd:'',
				newpwd:'',
				newpwd2:'',
				logining: false
			}				
		},
        computed: mapState(['hasLogin','userInfo']),
		onLoad() {
			 console.log('页面加载');
			this.loadData();
		},
        methods: {  
			...mapMutations(['logout','setUserInfo']),
            async loadData() {  
                if (!this.hasLogin) {  
					uni.navigateTo({
						url:'../login',
					})
                }else{
					this.memberInfo = this.userInfo;

				}
            },
			formSubmit: function (e) {
				var _this =this;
				
				if(_this.opwd.length<6||_this.opwd.length>20){ 
					uni.showToast({title: '请填写正确的旧密码',icon:"none"});
					return false; 
				}
				if(!(/^(?=.*?[a-z)(?=.*>[A-Z])(?=.*?[0-9])[a-zA_Z0-9]{6,10}$/.test(_this.newpwd)) ){ 
					uni.showToast({title: '密码必须大于6位包含数字和字母',icon:"none"});
					return false; 
				}
				if(_this.newpwd!=_this.newpwd2 ){ 
					uni.showToast({title: '两次输入的密码不一致',icon:"none"});
					return false; 
				}
				_this.logining =true;
				console.log('-----check-----------');
				_this.$Request.post('member_account/modify_pwd',{opwd:_this.opwd,newpwd:_this.newpwd,newpwd2:_this.newpwd2}).then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.logout();
						uni.showToast({
							icon: 'none',
							title:'修改成功！',
							duration:2000,
							success: function () {
								 setTimeout(function () {
									uni.redirectTo({
										url:'../login',
									});
								 }, 2000);
							}
						});
					}
					else{
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000
						});
						_this.logining = false;
					}
				}).catch(function (err) {
					console.log(err);
				  });
			},
			toPage(url){
				uni.navigateTo({
					url:url
				})
			},
			 
        }  
    }  
